/* ------------------------ MENU ------------------------- */

.menu {
    position: relative;
  }
  
  .menu .items {
    position: absolute;
    z-index: 10;
    top: 60px;
    right: 0;
    background-color: #203139;
    border-radius: 2px;
    padding: 10px;
  }
  
  .menu .items button {
    background-color: transparent;
    padding: 8px;
    color: white;
  }
  
  .menu .items button:hover {
    text-decoration: underline;
    cursor: pointer;
  }
  
  .menu-btn {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 10px;
    color: white;
    background-color: rgba(211, 211, 211, 0.05);
    border: 1px solid transparent;
  }